.button::before {
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -webkit-box-shadow: #959595 0 2px 5px;
  -moz-box-shadow: #959595 0 2px 5px;
  border-radius: 3px;
  box-shadow: #959595 0 2px 5px;
  content: "";
  display: block;
  height: 100%;
  left: 0;
  padding: 2px 0 0;
  position: absolute;
  top: 0;
  width: 100%; 
}
    
.button:active::before { 
  padding: 1px 0 0; 
}

.button {
  -moz-box-shadow: inset 0 0 0 1px #63ad0d;
  -webkit-box-shadow: inset 0 0 0 1px #63ad0d;
  -moz-border-radius: 3px;
  -webkit-border-radius: 3px;
  background: #eee;
  background: -webkit-gradient(linear, 0 0, 0 bottom, from(#eee), to(#e2e2e2));
  background: -webkit-gradient(linear, left top, left bottom, from(#eee), to(#e2e2e2));
  background: -webkit-linear-gradient(#eee, #e2e2e2);
  background: -o-linear-gradient(#eee, #e2e2e2);
  background: linear-gradient(#eee, #e2e2e2);
  border: solid 1px #d0d0d0;
  border-bottom: solid 3px #b2b1b1;
  border-radius: 3px;
  -webkit-box-shadow: inset 0 0 0 1px #f5f5f5;
          box-shadow: inset 0 0 0 1px #f5f5f5;
  color: #555;
  display: inline-block;
  font: bold 20px Arial, Helvetica, Clean, sans-serif;
  margin: 0 25px 25px 0;
  padding: 10px 20px;
  position: relative;
  text-align: center;
  text-decoration: none;
  text-shadow: 0 1px 0 #fafafa; 
}
    
.button:hover {
  background: #e4e4e4;
  background: -webkit-gradient(linear, 0 0, 0 bottom, from(#e4e4e4), to(#ededed));
  background: -webkit-gradient(linear, left top, left bottom, from(#e4e4e4), to(#ededed));
  background: -webkit-linear-gradient(#e4e4e4, #ededed);
  background: -o-linear-gradient(#e4e4e4, #ededed);
  background: linear-gradient(#e4e4e4, #ededed);
  border: solid 1px #c2c2c2;
  border-bottom: solid 3px #b2b1b1;
  -webkit-box-shadow: inset 0 0 0 1px #efefef;
          box-shadow: inset 0 0 0 1px #efefef; 
}
    
.button:active {
  background: #dfdfdf;
  background: -webkit-gradient(linear, 0 0, 0 bottom, from(#dfdfdf), to(#e3e3e3));
  background: -webkit-gradient(linear, left top, left bottom, from(#dfdfdf), to(#e3e3e3));
  background: -webkit-linear-gradient(#dfdfdf, #e3e3e3);
  background: -o-linear-gradient(#dfdfdf, #e3e3e3);
  background: linear-gradient(#dfdfdf, #e3e3e3);
  border: solid 1px #959595;
  -webkit-box-shadow: inset 0 10px 15px 0 #c4c4c4;
          box-shadow: inset 0 10px 15px 0 #c4c4c4;
  top:2px;
}

.button.green {
    background: #cae285;
    background: -webkit-gradient(linear, 0 0, 0 bottom, from(#cae285), to(#a3cd5a));
    background: -webkit-gradient(linear, left top, left bottom, from(#cae285), to(#a3cd5a));
    background: -webkit-linear-gradient(#cae285, #a3cd5a);
    background: -o-linear-gradient(#cae285, #a3cd5a);
    background: linear-gradient(#cae285, #a3cd5a);
    border: solid 1px #aad063;
    border-bottom: solid 3px #799545;
    -webkit-box-shadow: inset 0 0 0 1px #e0eeb6;
            box-shadow: inset 0 0 0 1px #e0eeb6;
    color: #5d7731;
    text-shadow: 0 1px 0 #d0e5a4; 
}
    
.button.green:hover {
    background: #abd164;
    background: -webkit-gradient(linear, 0 0, 0 bottom, from(#abd164), to(#b9d972));
    background: -webkit-gradient(linear, left top, left bottom, from(#abd164), to(#b9d972));
    background: -webkit-linear-gradient(#abd164, #b9d972);
    background: -o-linear-gradient(#abd164, #b9d972);
    background: linear-gradient(#abd164, #b9d972);
    border: solid 1px #98b85b;
    border-bottom: solid 3px #799545;
    -webkit-box-shadow: inset 0 0 0 1px #cce3a1;
            box-shadow: inset 0 0 0 1px #cce3a1; 
}
    
.button.green:active {
    background: #a4cb5d;
    background: -webkit-gradient(linear, 0 0, 0 bottom, from(#a4cb5d), to(#9ec45a));
    background: -webkit-gradient(linear, left top, left bottom, from(#a4cb5d), to(#9ec45a));
    background: -webkit-linear-gradient(#a4cb5d, #9ec45a);
    background: -o-linear-gradient(#a4cb5d, #9ec45a);
    background: linear-gradient(#a4cb5d, #9ec45a);
    border: solid 1px #6e883f;
    -webkit-box-shadow: inset 0 10px 15px 0 #90b352;
            box-shadow: inset 0 10px 15px 0 #90b352; 
}


