.containerGrad{
    margin: 0 auto;
    margin-top: 20px;
    min-width: 615px;
    max-width: 615px;
    min-height: 655px;
    background: #eae8e8; 
    border-radius: 3%;
    -webkit-box-shadow: 0 8px 15px rgba(0,0,0.6);
            box-shadow: 0 8px 15px rgba(0,0,0.6); 
   position: relative;
}

.containerGrad:after{
	content: "";
	width: 615px;
	height: 655px;
    border-radius: 3%;
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	
}

.container{
    margin: 0 auto;
    margin-top: 20px;
    min-width: 615px;
    max-width: 615px;
    min-height: 655px;
    background: #eae8e8; 
    border-radius: 3%;
    -webkit-box-shadow: 0 8px 15px rgba(0,0,0.6);
            box-shadow: 0 8px 15px rgba(0,0,0.6); 
   position: relative;
}


